                return function (){
                    const $el = this.$el;
                    if(!$el) return;
                    let link = this.settings.link_redirect;
                    const rootId = this.id;
                    const $this = this;
                    function ecomContactRedirect(){
                        if(!link) return false;
                        let href = link.href;
                        if(href=='') return false;
                        let target = link.target;
                        let url = window.location.href;
                        if(url.includes('contact_posted=true')){
                            if(target === '_blank'){
                                window.open(href);
                            }
                            else{
                                window.location.href = href;
                            }
                        }
                    }
                    ecomContactRedirect();
                    //
                    const form = $el.querySelector('#ecom-contact-form-' + rootId);
                    if(!form) return;
                    const submitBtn = form.querySelector('.ecom__form-button');
                    if(!submitBtn) return;

                    //Check if there is any check box type and required
                    let requiredCheckBoxList = $el.querySelectorAll('.ecom-required--checkbox');

                    submitBtn.addEventListener('click', function(e) {
                        if(requiredCheckBoxList.length > 0) {
                            requiredCheckBoxList.forEach(function(checkboxGroup) {
                                let checkbox_items = checkboxGroup.querySelectorAll('input[type=checkbox]');
                                if(checkbox_items.length == 0) return;
                                let isChecked = false;

                                checkbox_items.forEach(function(checkbox) {
                                    if(checkbox.checked) {
                                        isChecked = true;
                                    }
                                })

                                if(isChecked) {
                                    checkbox_items.forEach(function(checkbox) {
                                        checkbox.required = false;
                                    })
                                } else {
                                    checkbox_items.forEach(function(checkbox) {
                                        checkbox.required = true;
                                    })
                                }
                            })
                        }


                    })
                    if(form.dataset.ecTrackingId)
                    {
                        form.addEventListener('submit', function(){
                            if(window.Shopify && window.Shopify.analytics)
                            {
                                Shopify.analytics.publish("ec_custom_events",{
                                    "button_id": form.id,
                                    "tracking_id": form.dataset.ecTrackingId
                                })
                            }
                        },{once: true});
                    }

                }
            },
            isFullWidth () {
                return this.data?.settings?.fields &&
                    this.data.settings?.fields.every(field => field?.settings?.width === '100%');
            },
            fields () {
                return this.data && this.data.settings && 'fields' in this.data.settings
                    ? this.data.settings?.fields
